						</li>
						<li>
							<input type="radio" name="methodRadio" id="cycle-times">
							<label for="cycle-times">周期次数</label>
							<span>账号每天抽取</span>
							<input type="number" placeholder="1">
							<span>次</span>
						</li>
					</ul>
				</section>
				<section>
					<h4>中奖名单滚动：</h4>
					<ul class="lottery-method">
						<li>
							<input type="radio" name="methodScroll" id="fixed-times" checked="true">
							<label for="fixed-times">显示滚动</label>
						</li>
						<li>
							<input type="radio" name="methodScroll" id="cycle-times">
							<label for="cycle-times">关闭</label>
						</li>
					</ul>
				</section>
				<section>
					<h4>限制中奖次数：</h4>
					<ul class="lottery-method">
						<li>
							<input type="radio" name="methodTimes" id="fixed-times" checked="true">
							<label for="fixed-times">开启</label>
							<span>每次账号限中</span>
							<input type="number" placeholder="1">
							<span>次</span>
						</li>
						<li>
							<input type="radio" name="methodTimes" id="cycle-times">
							<label for="cycle-times">关闭</label>
						</li>
					</ul>
				</section>
				<section>
					<h4>限制参与人数：</h4>
					<ul class="lottery-method">
						<li>
							<input type="radio" name="methodPeople" id="fixed-times" checked="true">
							<label for="fixed-times">开启</label>
							<span>仅限</span>
							<input type="number" placeholder="1">
							<span>人参与</span>
						</li>
						<li>
							<input type="radio" name="methodPeople" id="cycle-times">
							<label for="cycle-times">关闭</label>
